import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Helmet } from "react-helmet";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGooglePlusG, faFacebookF, faGithub, faLinkedinIn } from "@fortawesome/free-brands-svg-icons";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import styles from "./login.module.css";

// Mock data
const mockUser = {
  id: "82815496",
  ten: "Nguyn Vn A",
  email: "nguyenvana@example.com",
  mat_khau: "password123",
  thoi_gian_het_han_hoi_vien: "2025-12-31T23:59:59Z",
};

const LoginPage = ({ onLoginSuccess }) => {
  const navigate = useNavigate();
  const [isSignUp, setIsSignUp] = useState(false);
  const [formData, setFormData] = useState({
    ten: "",
    emailOrUsername: "",
    mat_khau: "",
  });
  const [errorMessage, setErrorMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const toggleSignUp = () => setIsSignUp((prev) => !prev);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const validateForm = () => {
    if (!formData.emailOrUsername.trim() || !formData.mat_khau.trim()) {
      return "Vui lng in y  email/tn ngi dng v mt khu";
    }
    if (isSignUp && !formData.ten.trim()) {
      return "Vui lng in h v tn";
    }
    if (isSignUp && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.emailOrUsername)) {
      return "Email khng hp l";
    }
    if (formData.mat_khau.length < 6) {
      return "Mt khu phi c t nht 6 k t";
    }
    return null;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setErrorMessage("");

    // Kim tra validation
    const validationError = validateForm();
    if (validationError) {
      setErrorMessage(validationError);
      setIsLoading(false);
      return;
    }

    try {
      if (isSignUp) {
        // M phng ng k thnh cng
        setErrorMessage("ng k thnh cng! Vui lng ng nhp.");
        toggleSignUp();
      } else {
        // M phng ng nhp
        if (
          (formData.emailOrUsername === mockUser.email ||
            formData.emailOrUsername === mockUser.ten) &&
          formData.mat_khau === mockUser.mat_khau
        ) {
          // M phng token
          const token = "mock-jwt-token-12345";
          localStorage.setItem("token", token);

          // M phng thng tin ngi dng t API /auth/me
          const user = {
            id: mockUser.id,
            ten: mockUser.ten,
            thoi_gian_het_han_hoi_vien: mockUser.thoi_gian_het_han_hoi_vien,
          };

          // Gi onLoginSuccess vi d liu ngi dng
          onLoginSuccess(user);
          setErrorMessage("");
          navigate("/");
        } else {
          throw new Error("Email/tn ngi dng hoc mt khu khng ng");
        }
      }
    } catch (error) {
      setErrorMessage(error.message || "C li xy ra, vui lng th li");
    } finally {
      setIsLoading(false);
    }
  };

  const handleSocialLogin = (platform) => {
    setErrorMessage(`ng nhp bng ${platform} hin cha c h tr.`);
  };

  return (
    <>
      <Helmet>
        <title>{isSignUp ? "ng k - MyApp" : "ng nhp - MyApp"}</title>
      </Helmet>
      <div className="fullscreen-bg">
        <div className={`${styles.container} ${isSignUp ? styles.active : ""}`}>
          {/* Form ng k */}
          <div className={`${styles.formContainer} ${styles.signUp}`}>
            <form onSubmit={handleSubmit}>
              <h1>To ti khon</h1>
              <div className="social-icons">
                {[
                  { icon: faGooglePlusG, platform: "Google" },
                  { icon: faFacebookF, platform: "Facebook" },
                  { icon: faGithub, platform: "GitHub" },
                  { icon: faLinkedinIn, platform: "LinkedIn" },
                ].map(({ icon, platform }, index) => (
                  <a
                    key={index}
                    href="/"
                    className={styles.icon}
                    onClick={(e) => {
                      e.preventDefault();
                      handleSocialLogin(platform);
                    }}
                  >
                    <FontAwesomeIcon icon={icon} />
                  </a>
                ))}
              </div>
              {errorMessage && <div className={styles.error}>{errorMessage}</div>}
              <span>hoc s dng email ca bn  ng k</span>
              <input
                type="text"
                placeholder="H v tn"
                name="ten"
                value={formData.ten}
                onChange={handleChange}
                required
                disabled={isLoading}
              />
              <input
                type="email"
                placeholder="Email"
                name="emailOrUsername"
                value={formData.emailOrUsername}
                onChange={handleChange}
                required
                disabled={isLoading}
              />
              <input
                type="password"
                placeholder="Mt khu"
                name="mat_khau"
                value={formData.mat_khau}
                onChange={handleChange}
                required
                disabled={isLoading}
              />
              <button type="submit" disabled={isLoading}>
                {isLoading ? "ang x l..." : "ng k"}
              </button>
            </form>
          </div>

          {/* Form ng nhp */}
          <div className={`${styles.formContainer} ${styles.signIn}`}>
            <form onSubmit={handleSubmit}>
              <h1>ng nhp</h1>
              <div className="social-icons">
                {[
                  { icon: faGooglePlusG, platform: "Google" },
                  { icon: faFacebookF, platform: "Facebook" },
                  { icon: faGithub, platform: "GitHub" },
                  { icon: faLinkedinIn, platform: "LinkedIn" },
                ].map(({ icon, platform }, index) => (
                  <a
                    key={index}
                    href="/"
                    className={styles.icon}
                    onClick={(e) => {
                      e.preventDefault();
                      handleSocialLogin(platform);
                    }}
                  >
                    <FontAwesomeIcon icon={icon} />
                  </a>
                ))}
              </div>
              {errorMessage && <div className={styles.error}>{errorMessage}</div>}
              <span>hoc s dng email v mt khu ca bn</span>
              <input
                type="text"
                placeholder="Email hoc tn ngi dng"
                name="emailOrUsername"
                value={formData.emailOrUsername}
                onChange={handleChange}
                required
                disabled={isLoading}
              />
              <input
                type="password"
                placeholder="Mt khu"
                name="mat_khau"
                value={formData.mat_khau}
                onChange={handleChange}
                required
                disabled={isLoading}
              />
              <a href="/" onClick={(e) => e.preventDefault()}>
                Qun mt khu?
              </a>
              <button type="submit" disabled={isLoading}>
                {isLoading ? "ang x l..." : "ng nhp"}
              </button>
            </form>
          </div>

          {/* Hiu ng chuyn i */}
          <div className={styles.toggleContainer}>
            <div className={styles.toggle}>
              <div className={`${styles.togglePanel} ${styles.toggleLeft}`}>
                <h1>Cho mng tr li!</h1>
                <p>Nhp thng tin c nhn ca bn  s dng tt c cc tnh nng</p>
                <button className={styles.hidden} onClick={toggleSignUp} disabled={isLoading}>
                  ng nhp
                </button>
              </div>
              <div className={`${styles.togglePanel} ${styles.toggleRight}`}>
                <h1>Xin cho, bn!</h1>
                <p>ng k vi thng tin c nhn ca bn  s dng tt c cc tnh nng</p>
                <button className={styles.hidden} onClick={toggleSignUp} disabled={isLoading}>
                  ng k
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default LoginPage;