import React, { useState, useEffect, useRef } from "react";
import { Button } from "primereact/button";
import { Avatar } from "primereact/avatar";
import { InputTextarea } from "primereact/inputtextarea";
import { Rating } from "primereact/rating";
import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";
import { ConfirmDialog, confirmDialog } from "primereact/confirmdialog";
import { Toast } from "primereact/toast";
import "./UserPage.css";
import { useLocation, useNavigate } from "react-router-dom";

// Mock data
const mockUser = {
  id: 1,
  name: "Nguyn Vn A",
  status: "Thnh vin VIP",
  avatar: "https://png.pngtree.com/png-clipart/20200701/original/pngtree-cat-default-avatar-png-image_5416936.jpg",
  role: "Tn thit k c bn",
  position: "Tiu  cng vic ca bn",
  email: "user@example.com",
  password: "********",
  quyen: "user",
  so_dien_thoai: "0123456789",
  thoi_quan_het_han_han_hoi_vien: "2025-12-31",
};

const mockDownloadHistory = [
  {
    id: 1,
    type: "PowerPoint",
    title: "Mu PowerPoint Kinh doanh",
    image: "https://via.placeholder.com/80",
    description: "M t PowerPoint kinh doanh...",
    author: "Nguyen Van",
    size: "1200 * 1024",
    status: "download-link",
    date: "2024-06-20T10:00:00Z",
    powerPointId: 101,
    downloadLink: "/files/business-ppt.pptx",
  },
  {
    id: 2,
    type: "Hnh nh",
    title: "Hnh nh Sng",
    image: "https://via.placeholder.com/80",
    description: "M t hnh nh sng to...",
    author: "Le Thi",
    size: "1920 * 1080",
    status: "Free",
    date: "2024-06-15T14:30:00Z",
    hinhAnhId: 102,
    downloadLink: "/img/creative-image.jpg",
  },
];

const mockReviews = [
  {
    id: 1,
    content: "Mu PowerPoint rt p v d s dng!",
    rating: 5,
    date: "2024-10-01T12:00:00Z",
    powerPointId: 101,
  },
  {
    id: 2,
    content: "Hnh nh cht lng cao, ph hp cho d n ca ti.",
    rating: 4,
    date: "2024-09-15T15:00:00Z",
    powerPointId: null,
  },
];

const mockPurchasedItems = [
  {
    id: 1,
    type: "PowerPoint",
    title: "Mu PowerPoint Doanh nghip",
    image: "https://via.placeholder.com/80",
    description: "M t mu PowerPoint doanh nghip...",
    price: 75000,
    purchaseDate: "2024-06-10T09:00:00Z",
    powerPointId: 103,
    duong_dan_tap_tin: "/files/enterprise-ppt.pptx",
  },
  {
    id: 2,
    type: "Hnh nh",
    title: "Hnh nh Thin nhin",
    image: "https://via.placeholder.com/80",
    description: "M t hnh nh thin nhin...",
    price: 50000,
    purchaseDate: "2024-06-05T11:00:00Z",
    hinhAnhId: 104,
    duong_dan_tap_tin: "/img/nature-image.jpg",
  },
];

const mockMembershipHistory = [
  {
    id: 1,
    packageName: "Gi C Bn",
    description: "Gi hi vin c bn vi quyn truy cp 30 ngy",
    price: 99000,
    registrationDate: "2024-06-01T08:00:00Z",
    expiryDate: "2024-06-30T23:59:59Z",
    status: " kch hot",
  },
  {
    id: 2,
    packageName: "Gi Tiu Chun",
    description: "Gi hi vin tiu chun vi quyn truy cp 90 ngy",
    price: 249000,
    registrationDate: "2024-03-01T08:00:00Z",
    expiryDate: "2024-05-30T23:59:59Z",
    status: "Ht hn",
  },
];

const mockFavorites = [
  {
    id: 1,
    tieu_de: "Mu PowerPoint Sng to",
    duong_dan_anh_nho: "https://via.placeholder.com/80",
    la_pro: "Pro",
    mien_phi: false,
    duong_dan_tap_tin: "/files/creative-ppt.pptx",
  },
  {
    id: 2,
    tieu_de: "Hnh nh Ngh thut",
    duong_dan_anh_nho: "https://via.placeholder.com/80",
    la_pro: "Min ph",
    mien_phi: true,
    duong_dan_tap_tin: "/img/art-image.jpg",
  },
];

function UserPage() {
  const [activeSection, setActiveSection] = useState("profile");
  const [user, setUser] = useState(null);
  const [downloadHistory, setDownloadHistory] = useState([]);
  const [purchasedItems, setPurchasedItems] = useState([]);
  const [membershipHistory, setMembershipHistory] = useState([]);
  const [reviews, setReviews] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [filterType, setFilterType] = useState("PowerPoint");
  const [showReviewDialog, setShowReviewDialog] = useState(false);
  const [isEditingReview, setIsEditingReview] = useState(false);
  const [selectedPowerPointId, setSelectedPowerPointId] = useState(null);
  const [editingReview, setEditingReview] = useState(null);
  const [reviewForm, setReviewForm] = useState({
    rating: 5,
    comment: "",
  });
  const [isEditingProfile, setIsEditingProfile] = useState(false);
  const [editForm, setEditForm] = useState({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
    so_dien_thoai: "",
  });
  const [favorites, setFavorites] = useState([]);
  const [selectedItems, setSelectedItems] = useState([]);
  const toast = useRef(null);

  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    // Load mock favorites
    const storedFavorites = localStorage.getItem("favorites");
    let favoritesFromStorage = [];
    if (storedFavorites) {
      try {
        favoritesFromStorage = JSON.parse(storedFavorites);
        if (!Array.isArray(favoritesFromStorage)) {
          favoritesFromStorage = [];
        }
      } catch (error) {
        console.error("Error parsing favorites from localStorage:", error);
        favoritesFromStorage = [];
      }
    }

    const favoritesFromState = location.state?.favorites || [];
    const mergedFavorites = [
      ...mockFavorites, // Use mockFavorites as base
      ...favoritesFromStorage.filter(
        (storedFav) => !mockFavorites.some((mockFav) => mockFav.id === storedFav.id)
      ),
      ...favoritesFromState.filter(
        (stateFav) =>
          !mockFavorites.some((mockFav) => mockFav.id === stateFav.id) &&
          !favoritesFromStorage.some((storedFav) => storedFav.id === stateFav.id)
      ),
    ];
    setFavorites(mergedFavorites);
    localStorage.setItem("favorites", JSON.stringify(mergedFavorites));
  }, [location.state]);

  useEffect(() => {
    // Load mock user data
    try {
      setLoading(true);
      setError(null);

      // Set mock user
      setUser(mockUser);
      setEditForm({
        name: mockUser.name,
        email: mockUser.email,
        password: "",
        confirmPassword: "",
        so_dien_thoai: mockUser.so_dien_thoai,
      });

      // Set mock download history
      setDownloadHistory(mockDownloadHistory);

      // Set mock reviews
      setReviews(mockReviews);

      // Set mock purchased items
      setPurchasedItems(mockPurchasedItems);

      // Set mock membership history
      setMembershipHistory(mockMembershipHistory);
    } catch (err) {
      setError(err.message);
      console.error("Mock data error:", err);
    } finally {
      setLoading(false);
    }
  }, []);

  const sidebarItems = [
    { label: "Trung tm c nhn ca ti", key: "profile", icon: "pi pi-user" },
    { label: "Lch s ti v", key: "downloads", icon: "pi pi-download" },
    { label: "nh gi ca ti", key: "reviews", icon: "pi pi-star" },
    { label: "Theo di", key: "follow", icon: "pi pi-heart" },
    { label: "Mu  mua", key: "purchased", icon: "pi pi-shopping-cart" },
    { label: "Gi hi vin ca ti", key: "membership", icon: "pi pi-crown" },
  ];

  const handleEditProfile = () => {
    // Simulate profile update
    setUser({ ...user, name: editForm.name, email: editForm.email, so_dien_thoai: editForm.so_dien_thoai });
    setIsEditingProfile(false);
    toast.current.show({ severity: "success", summary: "Thnh cng", detail: "Cp nht thng tin thnh cng!" });
  };

  const handleDeleteAccount = () => {
    confirmDialog({
      message: "Bn c chc chn mun xa ti khon ny?",
      header: "Xc nhn xa",
      icon: "pi pi-exclamation-triangle",
      accept: () => {
        localStorage.removeItem("token");
        navigate("/login");
        toast.current.show({ severity: "success", summary: "Thnh cng", detail: "Ti khon  c xa!" });
      },
      reject: () => {},
    });
  };

  const handleFilterChange = (type) => {
    setFilterType(type);
  };

  const filteredDownloadHistory = downloadHistory.filter((item) => item.type === filterType);

  const openReviewDialog = (powerPointId) => {
    setSelectedPowerPointId(powerPointId);
    setShowReviewDialog(true);
  };

  const openEditReviewDialog = (review) => {
    setEditingReview(review);
    setReviewForm({ rating: review.rating, comment: review.content });
    setIsEditingReview(true);
    setShowReviewDialog(true);
  };

  const handleDeleteReview = (reviewId) => {
    setReviews(reviews.filter((review) => review.id !== reviewId));
    toast.current.show({ severity: "success", summary: "Thnh cng", detail: "nh gi  c xa!" });
  };

  const handleSelectAll = (e) => {
    if (e.target.checked) {
      setSelectedItems(favorites.map((ppt) => ppt.id));
    } else {
      setSelectedItems([]);
    }
  };

  const handleDeleteSelected = () => {
    setFavorites(favorites.filter((ppt) => !selectedItems.includes(ppt.id)));
    setSelectedItems([]);
    toast.current.show({ severity: "success", summary: "Thnh cng", detail: " xa cc mc chn!" });
  };

  const handleSelectItem = (id) => {
    setSelectedItems((prev) =>
      prev.includes(id) ? prev.filter((item) => item !== id) : [...prev, id]
    );
  };

  const handleDownload = (item) => {
    // Simulate download
    const link = document.createElement("a");
    link.href = item.duong_dan_tap_tin || item.downloadLink;
    link.download = item.title || "download";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    toast.current.show({ severity: "success", summary: "Thnh cng", detail: "Ti xung thnh cng!" });
  };

  const handleRemoveFavorite = (id) => {
    setFavorites(favorites.filter((ppt) => ppt.id !== id));
    localStorage.setItem("favorites", JSON.stringify(favorites.filter((ppt) => ppt.id !== id)));
    toast.current.show({ severity: "success", summary: "Thnh cng", detail: " xa khi danh sch yu thch!" });
  };

  const handlePowerPointClick = (item) => {
    // Simulate navigation to PowerPoint detail page
    console.log("PowerPoint clicked:", item);
  };

  const handleReviewSubmit = () => {
    const newReview = {
      id: reviews.length + 1,
      content: reviewForm.comment,
      rating: reviewForm.rating,
      powerPointId: selectedPowerPointId,
      date: new Date().toISOString(),
    };
    setReviews([...reviews, newReview]);
    setShowReviewDialog(false);
    toast.current.show({ severity: "success", summary: "Thnh cng", detail: "nh gi  c gi!" });
  };

  const handleEditReviewSubmit = () => {
    setReviews(
      reviews.map((review) =>
        review.id === editingReview.id ? { ...review, ...reviewForm } : review
      )
    );
    setShowReviewDialog(false);
    setIsEditingReview(false);
    setEditingReview(null);
    toast.current.show({ severity: "success", summary: "Thnh cng", detail: "nh gi  c cp nht!" });
  };

  if (loading) return <div className="text-center mt-10 text-gray-700">ang ti...</div>;
  if (error) return <div className="text-center mt-10 text-red-500">Li: {error}</div>;
  if (!user) return <div className="text-center mt-10 text-red-500">Khng tm thy thng tin ngi dng</div>;

  return (
    <div className="user-page">
      <Toast ref={toast} />
      <ConfirmDialog />
      <div className="sidebar">
        <ul>
          {sidebarItems.map((item) => (
            <li
              key={item.key}
              className={`sidebar-item ${activeSection === item.key ? "active" : ""}`}
              onClick={() => setActiveSection(item.key)}
            >
              <i className={item.icon}></i>
              <span>{item.label}</span>
            </li>
          ))}
        </ul>
      </div>
      <div className="main-content">
        {activeSection === "profile" && (
          <div className="profile">
            <div className="user-info">
              <Avatar image={user.avatar} size="xlarge" shape="circle" />
              <div className="user-details">
                <h3>
                  {user.name}
                  {user.thoi_quan_het_han_han_hoi_vien && (
                    <i
                      className="pi pi-crown"
                      style={{ color: "gold", marginLeft: "8px" }}
                      title="Thnh vin VIP"
                    />
                  )}
                </h3>
                <p>ID: {user.id}</p>
                <span className="user-status">{user.status}</span>
              </div>
              <Button label="Tr thnh thnh vin" className="upgrade-btn" />
            </div>
            <div className="social-buttons">
              <h4>Ti khon lin kt</h4>
              <Button
                label="Tip tc s dng Facebook"
                icon="pi pi-facebook"
                className="p-button-info"
              />
              <Button
                label="Tip tc s dng Google"
                icon="pi pi-google"
                className="p-button-danger"
              />
              <Button
                label="Tip tc s dng Twitter"
                icon="pi pi-twitter"
                className="p-button-secondary"
              />
            </div>
            <div className="user-data">
              <h4>D liu ca bn</h4>
              {isEditingProfile ? (
                <div className="edit-form">
                  <div className="data-item">
                    <label>Tn ngi dng *</label>
                    <InputText
                      value={editForm.name}
                      onChange={(e) => setEditForm({ ...editForm, name: e.target.value })}
                      placeholder="Nhp tn ngi dng"
                    />
                  </div>
                  <div className="data-item">
                    <label>Email *</label>
                    <InputText
                      value={editForm.email}
                      onChange={(e) => setEditForm({ ...editForm, email: e.target.value })}
                      placeholder="Nhp email"
                    />
                  </div>
                  <div className="data-item">
                    <label>Mt khu mi</label>
                    <InputText
                      type="password"
                      value={editForm.password}
                      onChange={(e) => setEditForm({ ...editForm, password: e.target.value })}
                      placeholder="Nhp mt khu mi ( trng nu khng i)"
                    />
                  </div>
                  <div className="data-item">
                    <label>Xc nhn mt khu mi</label>
                    <InputText
                      type="password"
                      value={editForm.confirmPassword}
                      onChange={(e) =>
                        setEditForm({ ...editForm, confirmPassword: e.target.value })
                      }
                      placeholder="Nhp li mt khu mi"
                    />
                  </div>
                  <div className="data-item">
                    <label>S in thoi</label>
                    <InputText
                      value={editForm.so_dien_thoai}
                      onChange={(e) =>
                        setEditForm({ ...editForm, so_dien_thoai: e.target.value })
                      }
                      placeholder="Nhp s in thoi"
                    />
                  </div>
                  <div className="data-item">
                    <Button
                      label="Lu"
                      icon="pi pi-check"
                      className="p-button-success"
                      onClick={handleEditProfile}
                    />
                    <Button
                      label="Hy"
                      icon="pi pi-times"
                      className="p-button-secondary"
                      onClick={() => setIsEditingProfile(false)}
                    />
                  </div>
                </div>
              ) : (
                <>
                  <div className="data-item">
                    <label>Tn thit k c bn</label>
                    <p>{user.role}</p>
                  </div>
                  <div className="data-item">
                    <label>Chc danh</label>
                    <p>{user.position}</p>
                  </div>
                  <Button
                    label="Chnh sa thng tin"
                    icon="pi pi-pencil"
                    className="p-button-primary"
                    onClick={() => setIsEditingProfile(true)}
                  />
                </>
              )}
            </div>
            <div className="account-info">
              <h4>Thng tin ti khon</h4>
              <div className="data-item">
                <label>Tn ngi dng *</label>
                <p>{user.name}</p>
              </div>
              <div className="data-item">
                <label>Email *</label>
                <p>{user.email}</p>
              </div>
              <div className="data-item">
                <label>Mt khu *</label>
                <p>{user.password}</p>
              </div>
              <div className="data-item">
                <label>Quyn</label>
                <p>{user.quyen || "Khng xc nh"}</p>
              </div>
              <div className="data-item">
                <label>S in thoi</label>
                <p>{user.so_dien_thoai || "Khng c"}</p>
              </div>
              <div className="data-item">
                <label>Thi gian ht hn hi vin</label>
                <p>{user.thoi_quan_het_han_han_hoi_vien || "Khng c"}</p>
              </div>
              <Button
                label="Xa ti khon"
                icon="pi pi-trash"
                className="p-button-danger"
                onClick={handleDeleteAccount}
              />
            </div>
          </div>
        )}
        {activeSection === "downloads" && (
          <div className="downloads">
            <h2>Lch s ti v ca ti</h2>
            <div className="filter-bar">
              <Button
                label="PowerPoint"
                className={`filter-btn ${filterType === "PowerPoint" ? "active" : ""}`}
                onClick={() => handleFilterChange("PowerPoint")}
              />
              <Button
                label="Hnh nh"
                className={`filter-btn ${filterType === "Hnh nh" ? "active" : ""}`}
                onClick={() => handleFilterChange("Hnh nh")}
              />
            </div>
            <div className="download-header">
              <span>Thng tin hnh nh</span>
              <span></span>
              <span>Thng tin ty chn</span>
            </div>
            {filteredDownloadHistory.length > 0 ? (
              filteredDownloadHistory.map((item) => (
                <div
                  key={`${item.powerPointId || item.hinhAnhId || item.id}`}
                  className="download-item"
                >
                  <div className="download-image-info">
                    <img src={item.image} alt={item.title} className="download-img" />
                    <div className="download-info">
                      <h4>{item.title}</h4>
                      <p>
                        Tc gi: {item.author} <br />
                        Kch thc: {item.size} <br />
                        Thi gian ti xung: {new Date(item.date).toLocaleString("vi-VN")}
                      </p>
                      {item.type === "PowerPoint" && (
                        <Button
                          label="Vit nh gi"
                          icon="pi pi-star"
                          className="p-button-sm p-button-secondary"
                          onClick={() => openReviewDialog(item.powerPointId)}
                        />
                      )}
                    </div>
                  </div>
                  <div className="download-status">
                    {item.status === "Need Credit" && (
                      <span className="need-credit">Need Credit!</span>
                    )}
                  </div>
                  <div className="download-permission">
                    <p>
                      <i className="pi pi-money-bill permission-icon free"></i> Ghi php min ph
                    </p>
                    <p>
                      <i className="pi pi-ban permission-icon non-commercial"></i> Khng s dng
                      thng mi
                    </p>
                  </div>
                </div>
              ))
            ) : (
              <p>Cha c lch s ti v cho {filterType}.</p>
            )}
          </div>
        )}
        {activeSection === "reviews" && (
          <div className="reviews">
            <h2>nh gi ca ti</h2>
            {reviews.length > 0 ? (
              reviews.map((item) => (
                <div key={item.id} className="review-item">
                  <div className="review-header">
                    <span className="review-user">{user.name}</span>
                    <Rating
                      value={item.rating}
                      readOnly
                      cancel={false}
                      className="review-rating"
                    />
                  </div>
                  <p className="review-content">{item.content}</p>
                  <p className="review-date">
                    Ngy nh gi: {new Date(item.date).toLocaleString("vi-VN")}
                  </p>
                  <div className="review-actions">
                    <Button
                      label="Sa"
                      icon="pi pi-pencil"
                      className="p-button-sm p-button-primary"
                      onClick={() => openEditReviewDialog(item)}
                    />
                    <Button
                      label="Xa"
                      icon="pi pi-trash"
                      className="p-button-sm p-button-danger"
                      onClick={() => handleDeleteReview(item.id)}
                    />
                  </div>
                </div>
              ))
            ) : (
              <p>Cha c nh gi no.</p>
            )}
          </div>
        )}
        {activeSection === "follow" && (
          <div className="follow">
            <h2>Danh sch yu thch</h2>
            {favorites.length > 0 ? (
              <>
                <div className="follow-header">
                  <div className="select-all-container">
                    <input
                      type="checkbox"
                      checked={selectedItems.length === favorites.length && favorites.length > 0}
                      onChange={handleSelectAll}
                    />
                    <span>Chn tt c</span>
                  </div>
                  <button
                    className="delete-selected-btn"
                    onClick={handleDeleteSelected}
                    disabled={selectedItems.length === 0}
                  >
                    Xa
                  </button>
                </div>
                <div className="follow-container">
                  {favorites.map((ppt) => (
                    <div className="follow-item" key={ppt.id}>
                      <input
                        type="checkbox"
                        checked={selectedItems.includes(ppt.id)}
                        onChange={() => handleSelectItem(ppt.id)}
                        className="follow-checkbox"
                      />
                      <div className="follow-image-info">
                        <img
                          src={ppt.duong_dan_anh_nho}
                          alt={ppt.tieu_de}
                          className="follow-img"
                        />
                        <div className="follow-info">
                          <h4>{ppt.tieu_de}</h4>
                          <p>{ppt.la_pro || "Min ph"}</p>
                        </div>
                      </div>
                      <div className="follow-actions">
                        {ppt.mien_phi && <span className="badge-free">Min ph</span>}
                        <button className="download-btn" onClick={() => handleDownload(ppt)}>
                          <i className="bx bx-download"></i>
                        </button>
                        <button
                          className="remove-favorite-btn"
                          onClick={() => handleRemoveFavorite(ppt.id)}
                        >
                          <i className="pi pi-trash"></i>
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              </>
            ) : (
              <p>Cha c PowerPoint no trong danh sch yu thch.</p>
            )}
          </div>
        )}
        {activeSection === "purchased" && (
          <div className="purchased">
            <h2>Mu  mua</h2>
            {purchasedItems.length > 0 ? (
              purchasedItems.map((item) => (
                <div key={item.id} className="follow-item">
                  <img
                    src={item.image}
                    alt={item.title}
                    className="follow-img"
                    onClick={() => handlePowerPointClick(item)}
                  />
                  <div className="follow-info">
                    <h4>{item.title}</h4>
                    <p>
                      Loi: {item.type} <br />
                      Gi: {item.price.toLocaleString("vi-VN")} VN <br />
                      Ngy mua: {new Date(item.purchaseDate).toLocaleString("vi-VN")} <br />
                    </p>
                  </div>
                  <div className="follow-actions">
                    <Button
                      label="Ti xung"
                      icon="pi pi-download"
                      className="p-button-sm p-button-success"
                      onClick={() => handleDownload(item)}
                    />
                  </div>
                </div>
              ))
            ) : (
              <p>Cha c mu no c mua.</p>
            )}
          </div>
        )}
        {activeSection === "membership" && (
          <div className="membership">
            <h2>Lch s hi vin</h2>
            {membershipHistory.length > 0 ? (
              membershipHistory.map((item) => (
                <div key={item.id} className="follow-item">
                  <h4>{item.packageName}</h4>
                  <p>
                    M t: {item.description} <br />
                    Gi: {item.price.toLocaleString("vi-VN")} VN <br />
                    Ngy ng k: {new Date(item.registrationDate).toLocaleString("vi-VN")} <br />
                    Ngy ht hn: {new Date(item.expiryDate).toLocaleString("vi-VN")} <br />
                    Trng thi: {item.status}
                  </p>
                </div>
              ))
            ) : (
              <p>Cha c lch s hi vin.</p>
            )}
          </div>
        )}
      </div>
      <Dialog
        header={isEditingReview ? "Sa nh gi" : "Vit nh gi"}
        visible={showReviewDialog}
        style={{ width: "50vw" }}
        onHide={() => {
          setShowReviewDialog(false);
          setIsEditingReview(false);
          setEditingReview(null);
          setReviewForm({ rating: 5, comment: "" });
        }}
      >
        <div className="p-fluid">
          <div className="p-field">
            <label>im nh gi</label>
            <Rating
              value={reviewForm.rating}
              onChange={(e) => setReviewForm({ ...reviewForm, rating: e.value })}
              cancel={false}
            />
          </div>
          <div className="p-field">
            <label>Bnh lun</label>
            <InputTextarea
              value={reviewForm.comment}
              onChange={(e) => setReviewForm({ ...reviewForm, comment: e.target.value })}
              rows={5}
              autoResize
            />
          </div>
          <Button
            label={isEditingReview ? "Cp nht nh gi" : "Gi nh gi"}
            icon="pi pi-check"
            onClick={isEditingReview ? handleEditReviewSubmit : handleReviewSubmit}
            disabled={!reviewForm.comment}
          />
        </div>
      </Dialog>
    </div>
  );
}

export default UserPage;