import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import "./VIPPage.css";

// Mock data for membership packages
const mockPackages = [
  {
    id: 1,
    ten_goi: "Gi C Bn",
    gia: 99000,
    thoi_han: 30,
  },
  {
    id: 2,
    ten_goi: "Gi Tiu Chun",
    gia: 249000,
    thoi_han: 90,
  },
  {
    id: 3,
    ten_goi: "Gi Cao Cp",
    gia: 499000,
    thoi_han: 180,
  },
];

const VIPPage = () => {
  const [packages, setPackages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  // Mock data loading
  useEffect(() => {
    // Simulate fetching packages
    setPackages(mockPackages);
    setLoading(false);
  }, []);

  const handleBuyNow = (pkg) => {
    navigate(`/checkout/${pkg.id}`, { state: { selectedPackage: pkg } });
  };

  if (loading) {
    return <div className="text-center mt-10 text-gray-700">ang ti...</div>;
  }

  if (error) {
    return <div className="text-center mt-10 text-red-500">Li: {error}</div>;
  }

  const blogs = [
    {
      img: "https://js.pngtree.com/a4/static/1gtrjrq.a76fff00.png",
      alt: "Imagen Blog 1",
      description: "i ng thit k c quyn ng o",
    },
    {
      img: "https://js.pngtree.com/a4/static/iqtcld.153b81b2.png",
      alt: "Imagen Blog 2",
      description: "Hip nh PRF cho giy php thng mi",
    },
    {
      img: "https://js.pngtree.com/a4/static/gjckia.325a0204.png",
      alt: "Imagen Blog 3",
      description: "Ti xung khng gii hn ti nguyn hnh nh a th loi",
    },
  ];

  return (
    <section className="top-categories-1">
      <h1 className="heading-1 top">Cc Gi Hi Vin</h1>
      <div className="container-vip">
        {packages.map((pkg) => (
          <div key={pkg.id} className="card-vip">
            <h3>{pkg.ten_goi}</h3>
            <div className="text-3xl font-bold text-orange-500 mb-2">
              {pkg.gia.toLocaleString("vi-VN")} VN
              <span className="text-sm text-gray-500 ml-1">
                ({(pkg.gia / 25000).toFixed(2)} USD)
              </span>
            </div>
            <ul className="text-gray-600 mb-6 text-left pl-5">
              <li className="flex items-center mb-2">
                <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                {pkg.thoi_han} ngy truy cp
              </li>
              <li className="flex items-center mb-2">
                <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                90 ngy th dng vi ti khon min ph
              </li>
              <li className="flex items-center mb-2">
                <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                Co ph
              </li>
              <li className="flex items-center mb-2">
                <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                Giy php s dng mi mi
              </li>
            </ul>
            <button
              className="bg-green-500 text-white font-semibold py-2 px-4 rounded hover:bg-green-600 transition duration-200"
              onClick={() => handleBuyNow(pkg)}
            >
              Mua ngay
            </button>
            <p className="text-gray-500 mt-2 text-sm">Hy bt c lc no</p>
          </div>
        ))}
      </div>
      {/* Blogs Section */}
      <section className="container blogs">
        <h1 className="heading-1">
          Hng chc triu ngi dng  tham gia Gi Premium C nhn ca chng ti
        </h1>
        <div className="container-blogs">
          {blogs.map((blog, index) => (
            <div className="card-blog" key={blog.alt}>
              <div className="container-img">
                <img src={blog.img} alt={blog.alt} width={250} height={250} />
                <div className="button-group-blog">
                  <span>
                    <i className="bx bx-search-alt"></i>
                  </span>
                  <span>
                    <i className="bx bx-link"></i>
                  </span>
                </div>
              </div>
              <div className="content-blog">
                <p>{blog.description}</p>
                <div className="btn-read-more">c thm</div>
              </div>
            </div>
          ))}
        </div>
      </section>
    </section>
  );
};

export default VIPPage;