import React, { useState, useEffect, useRef } from "react";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import "./ppt.css";
import "./App.css";
import { useLocation, useNavigate } from "react-router-dom";

// Mock data for detail images (PowerPoint or Image)
const mockDetailImages = [
  { src: "/img/detail1.png", alt: "nh chi tit 1" },
  { src: "/img/detail2.png", alt: "nh chi tit 2" },
  { src: "/img/detail3.png", alt: "nh chi tit 3" },
];

// Mock data for related PowerPoints
const mockRelatedPowerpoints = [
  { id: 1, tieu_de: "Mu PowerPoint Lch s", duong_dan_anh_nho: "/img/ppt1.png", la_pro: false, gia: 0 },
  { id: 2, tieu_de: "Mu PowerPoint Gio dc", duong_dan_anh_nho: "/img/ppt2.png", la_pro: true, gia: 50000 },
  { id: 3, tieu_de: "Mu PowerPoint Kinh doanh", duong_dan_anh_nho: "/img/ppt3.png", la_pro: false, gia: 0 },
  { id: 4, tieu_de: "Mu PowerPoint Cng ngh", duong_dan_anh_nho: "/img/ppt4.png", la_pro: true, gia: 75000 },
  { id: 5, tieu_de: "Mu PowerPoint Sng to", duong_dan_anh_nho: "/img/ppt5.png", la_pro: false, gia: 0 },
];

// Mock data for related Backgrounds
const mockRelatedBackgrounds = [
  { id: 1, tieu_de: "Hnh nn L Ging sinh", duong_dan_anh_nho: "/img/noel-bg.png", la_pro: false, gia: 0 },
  { id: 2, tieu_de: "Hnh nn m thc", duong_dan_anh_nho: "/img/ad3.jpg", la_pro: false, gia: 0 },
  { id: 3, tieu_de: "Hnh nn Quang cnh", duong_dan_anh_nho: "/img/landscape.jpg", la_pro: true, gia: 30000 },
  { id: 4, tieu_de: "Hnh nn ng vt", duong_dan_anh_nho: "/img/animal.jpg", la_pro: false, gia: 0 },
  { id: 5, tieu_de: "Hnh nn Vn hoa", duong_dan_anh_nho: "/img/flower.jpg", la_pro: true, gia: 40000 },
  { id: 6, tieu_de: "Hnh nn Bu tri", duong_dan_anh_nho: "/img/sky.jpg", la_pro: false, gia: 0 },
  { id: 7, tieu_de: "Hnh nn Cng ngh", duong_dan_anh_nho: "/img/tech.jpg", la_pro: true, gia: 50000 },
  { id: 8, tieu_de: "Hnh nn Ti gin", duong_dan_anh_nho: "/img/minimal.jpg", la_pro: false, gia: 0 },
];

// Mock data for reviews
const mockReviews = [
  { user: "Nguyn Vn A", rating: 4.5, comment: "Hnh nh rt p!", date: "2025-04-01" },
  { user: "Trn Th B", rating: 5, comment: "Rt ph hp cho thit k ca ti.", date: "2025-03-30" },
];

// Mock data for user
const mockUser = {
  thoi_gian_het_han_hoi_vien: "2025-12-31T23:59:59Z", // VIP valid until end of 2025
};

const PPTTemplate = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { powerpoint, image } = location.state || {};
  const item = powerpoint || image;
  const isPowerpoint = !!powerpoint;
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [detailImages, setDetailImages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [relatedPowerpoints, setRelatedPowerpoints] = useState([]);
  const [relatedBackgrounds, setRelatedBackgrounds] = useState([]);
  const [reviews, setReviews] = useState([]);
  const [isMembershipValid, setIsMembershipValid] = useState(false);
  const [loadingUser, setLoadingUser] = useState(true);
  const thumbnailRef = useRef(null);

  // Mock user check
  useEffect(() => {
    // Simulate checking user membership status
    const token = localStorage.getItem("token");
    if (token && mockUser.thoi_gian_het_han_hoi_vien) {
      const expiryDate = new Date(mockUser.thoi_gian_het_han_hoi_vien);
      const currentDate = new Date("2025-06-25T17:19:00+07:00"); // Current date
      setIsMembershipValid(expiryDate > currentDate);
    } else {
      setIsMembershipValid(false);
    }
    setLoadingUser(false);
  }, []);

  // Mock data loading
  useEffect(() => {
    if (item) {
      // Set detail images
      if (isPowerpoint) {
        setDetailImages(mockDetailImages);
      } else {
        setDetailImages([{ src: item.duong_dan_anh_nho, alt: item.tieu_de }]);
      }
      setLoading(false);

      // Set related PowerPoints and Backgrounds
      setRelatedPowerpoints(mockRelatedPowerpoints);
      setRelatedBackgrounds(mockRelatedBackgrounds);

      // Set reviews
      setReviews(mockReviews);
    }
  }, [item, isPowerpoint]);

  // Hm cun thumbnail sang tri
  const scrollThumbnailsLeft = () => {
    if (thumbnailRef.current) {
      thumbnailRef.current.scrollBy({ left: -100, behavior: "smooth" });
    }
  };

  // Hm cun thumbnail sang phi
  const scrollThumbnailsRight = () => {
    if (thumbnailRef.current) {
      thumbnailRef.current.scrollBy({ left: 100, behavior: "smooth" });
    }
  };

  const prevSlide = () => {
    setSelectedIndex((prev) => (prev === 0 ? detailImages.length - 1 : prev - 1));
  };

  const nextSlide = () => {
    setSelectedIndex((prev) => (prev === detailImages.length - 1 ? 0 : prev + 1));
  };

  const handleDownload = () => {
    const fileUrl = isPowerpoint ? item.duong_dan_tap_tin : item.duong_dan_anh_nho;
    const filename = fileUrl ? fileUrl.split("/").pop() : item.tieu_de;

    if (!fileUrl) {
      alert("Khng tm thy ng dn tp tin  ti xung.");
      return;
    }

    if (isMembershipValid) {
      downloadFile(fileUrl, filename);
      return;
    }

    if (item.la_pro) {
      const dialog = document.createElement("div");
      dialog.className = "dialog-overlay";
      dialog.innerHTML = `
        <div class="dialog-content">
          <button class="dialog-close"></button>
          <h2>M kha ni dung cao cp</h2>
          <p>Chn mt trong cc ty chn di y  tip tc:</p>
          <div class="download-options">
            <div class="option">
              <button class="option-btn slow">
                <span class="option-icon"></span>
                <button class="dialog-button buy-now-option">Mua ngay - ${item.gia || 0} VN</button>
                <span class="option-speed">Tc  ti v 0.5MB/s</span>
              </button>
              <ul class="option-details">
                <li>Mua ngay  ti mu cao cp</li>
                <li>Tip cn 1 ting ti sn</li>
                <li>Ph hng lot khng</li>
                <li>Khng s dng thng mi</li>
              </ul>
            </div>
            <div class="option">
              <button class="option-btn fast">
                <span class="option-icon"></span>
                <button class="dialog-button membership-option">Mua gi hi vin</button>
                <span class="option-speed">Tc  ti v 10MB/s</span>
              </button>
              <ul class="option-details">
                <li>Ti khon gii hn</li>
                <li>Tip cn 8 ting ti sn</li>
                <li>Khng cn ph ch thch ngun vi tc phm</li>
                <li>S dng thng mi c nhn</li>
              </ul>
            </div>
          </div>
          <p class="dialog-cancel">Hy bt c lc no</p>
        </div>
      `;
      document.body.appendChild(dialog);

      const closeButton = dialog.querySelector(".dialog-close");
      closeButton.addEventListener("click", () => {
        document.body.removeChild(dialog);
      });

      const membershipButton = dialog.querySelector(".membership-option");
      membershipButton.addEventListener("click", () => {
        document.body.removeChild(dialog);
        navigate("/vip");
      });

      const buyNowButton = dialog.querySelector(".buy-now-option");
      buyNowButton.addEventListener("click", () => {
        document.body.removeChild(dialog);
        navigate("/checkout/1", {
          state: {
            selectedPackage: {
              id: item.id,
              tieu_de: item.tieu_de,
              gia: item.gia || 0,
              type: isPowerpoint ? "powerpoint" : "image",
              duong_dan_tep_tin: fileUrl,
              duong_dan_anh_nho: fileUrl,
            },
          },
        });
      });
    } else {
      const dialog = document.createElement("div");
      dialog.className = "dialog-overlay";
      dialog.innerHTML = `
        <div class="dialog-content">
          <button class="dialog-close"></button>
          <h2>Chn phng thc ti v</h2>
          <div class="download-options">
            <div class="option">
              <button class="option-btn slow">
                <span class="option-icon"></span>
                <span class="option-title">Th gin ti xung</span>
                <span class="option-speed">Tc  ti v 0.5MB/s</span>
              </button>
              <ul class="option-details">
                <li>1 lt ti xung mi ngy</li>
                <li>Tip cn 1 ting ti sn</li>
                <li>Ph hng lot khng</li>
                <li>Khng s dng thng mi</li>
              </ul>
            </div>
            <div class="option">
              <button class="option-btn fast">
                <span class="option-icon"></span>
                <span class="option-title">Ti nhanh</span>
                <span class="option-speed">Tc  ti v 10MB/s</span>
              </button>
              <ul class="option-details">
                <li>Ti khon gii hn</li>
                <li>Tip cn 8 ting ti sn</li>
                <li>Khng cn ph ch thch ngun vi tc phm</li>
                <li>S dng thng mi c nhn</li>
              </ul>
            </div>
          </div>
        </div>
      `;
      document.body.appendChild(dialog);

      const closeButton = dialog.querySelector(".dialog-close");
      const slowButton = dialog.querySelector(".slow");
      const fastButton = dialog.querySelector(".fast");

      closeButton.addEventListener("click", () => {
        document.body.removeChild(dialog);
      });

      slowButton.addEventListener("click", () => {
        document.body.removeChild(dialog);
        downloadFile(fileUrl, filename);
      });

      fastButton.addEventListener("click", () => {
        document.body.removeChild(dialog);
        navigate("/vip");
      });
    }
  };

  const downloadFile = (url, filename) => {
    const link = document.createElement("a");
    link.href = url;
    link.download = filename || url.split("/").pop();
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const averageRating =
    reviews.length > 0
      ? (reviews.reduce((sum, review) => sum + review.rating, 0) / reviews.length).toFixed(1)
      : 0;

  if (!item) {
    return <div>Khng c d liu no c chn.</div>;
  }

  if (loadingUser) {
    return <div>ang kim tra trng thi ti khon...</div>;
  }

  return (
    <div className="ppt-container">
      <p className="ppt-description">
        Trang ch <i className="bx bx-chevron-right"></i> {isPowerpoint ? "PowerPoint" : "Hnh nh"}
      </p>
      <h1 className="ppt-title">{item.tieu_de}</h1>

      <div className="ppt-content">
        <div className="ppt-image-container">
          {loading ? (
            <p>ang ti nh chi tit...</p>
          ) : detailImages.length === 0 ? (
            <img src={item.duong_dan_anh_nho} alt={item.tieu_de} className="ppt-image" />
          ) : (
            <>
              <img
                src={detailImages[selectedIndex].src}
                alt={detailImages[selectedIndex].alt}
                className="ppt-image"
              />
              {detailImages.length > 1 && (
                <>
                  <div className="ppt-counter">
                    {selectedIndex + 1}/{detailImages.length}
                  </div>
                  <button className="ppt-nav ppt-prev" onClick={prevSlide}>
                    
                  </button>
                  <button className="ppt-nav ppt-next" onClick={nextSlide}>
                    
                  </button>
                  <div className="ppt-thumbnails-container">
                    <button className="thumbnail-nav left" onClick={scrollThumbnailsLeft}>
                      
                    </button>
                    <div className="ppt-thumbnails" ref={thumbnailRef}>
                      {detailImages.map((img, index) => (
                        <img
                          key={img.src}
                          src={img.src}
                          alt={img.alt}
                          className={`ppt-thumbnail ${selectedIndex === index ? "active" : ""}`}
                          onClick={() => setSelectedIndex(index)}
                        />
                      ))}
                    </div>
                    <button className="thumbnail-nav right" onClick={scrollThumbnailsRight}>
                      
                    </button>
                  </div>
                </>
              )}
            </>
          )}
          <div className="ppt-detail">
            <h2>{item.tieu_de}</h2>
            <p>
              <h3>Mu PowerPoint v Hnh nh ti min ph</h3>
            </p>
            <p>{item.mo_ta || "Khng c m t."}</p>
            <p>
              Loi: <span className="text-highlight">{item.la_pro ? "Pro" : "Min ph"}</span>
            </p>
            {item.la_pro && item.gia && (
              <p>
                Gi: <span className="text-highlight">{item.gia} VN</span>
              </p>
            )}
            <p>
              Ch : <span className="text-highlight">Lch s, Gio dc, n gin, K hoch</span>
            </p>
          </div>

          <div className="ppt-tags">
            <span className="tag">Lch s</span>
            <span className="tag">Gio dc</span>
            <span className="tag">Mu ti gin</span>
            <span className="tag">K hoch</span>
            <span className="tag">Nm</span>
            <span className="tag">nh gi hiu sut</span>
            <span className="tag tag-more">+22</span>
          </div>
        </div>

        <div className="ppt-info">
          <p className="ppt-description">
            {isPowerpoint
              ? "Mu PowerPoint ny c giy php bn quyn v c sn  s dng thng mi."
              : "Hnh nh ny c giy php bn quyn v c sn  s dng thng mi."}{" "}
            Nng cp ln gi Premium c nhn{" "}
            <span className="text-orange">
              Hoc Nng cp ln thnh vin cao cp ca cng ty nhin chnh hng  giy php.
            </span>{" "}
            <a href="/vip" className="text-green">
              Bm vo y
            </a>
          </p>
          <button
            className="ppt-btn ppt-btn-orange"
            onClick={handleDownload}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              width: "100%",
              padding: "10px",
              fontSize: "16px",
            }}
          >
            <span style={{ marginRight: "5px" }}>
              <i className="bx bxs-download"></i>
            </span>{" "}
            {isPowerpoint ? "PowerPoint" : "Hnh nh"}
          </button>

          <div
            className="ppt-btn-group"
            style={{ display: "flex", justifyContent: "space-between", margin: "10px 0" }}
          >
            <button className="ppt-btn text-gray" style={{ flex: 1, marginRight: "5px" }}>
              <i className="bx bxs-info-circle"></i> Thng tin
            </button>
            <button className="ppt-btn text-gray" style={{ flex: 1, marginRight: "5px" }}>
              <i className="bx bxs-heart"></i> Gi
            </button>
            <button className="ppt-btn text-gray" style={{ flex: 1 }}>
              <i className="bx bxs-share-alt"></i> Chia s
            </button>
          </div>

          <h3 className="ppt-license" style={{ fontSize: "16px", margin: "10px 0" }}>
            Phm vi y quyn: <span className="text-orange">Giy php thng mi</span>
          </h3>
          <h4 style={{ fontSize: "14px", margin: "5px 0" }}>
            i tng c y quyn: <span className="text-green">C nhn</span>{" "}
            <span className="text-gray">Doanh nghip</span>
          </h4>

          <div className="ppt-license-details" style={{ margin: "10px 0" }}>
            <img
              src="/img/giayphep.jpg"
              alt="Giy php"
              style={{ width: "100%", marginBottom: "10px" }}
            />
            <ul style={{ listStyle: "none", padding: "0" }}>
              <li style={{ display: "flex", alignItems: "center", marginBottom: "10px" }}>
                <span style={{ marginRight: "10px" }}></span> m bo bn quyn
              </li>
              <li style={{ display: "flex", alignItems: "center", marginBottom: "10px" }}>
                <span style={{ marginRight: "10px" }}></span> Giy php PRF cho mc ch thng mi c nhn
              </li>
              <li style={{ display: "flex", alignItems: "center", marginBottom: "10px" }}>
                <span style={{ marginRight: "10px" }}></span> Khng cn ph ch thch ngun tc phm
              </li>
              <li style={{ display: "flex", alignItems: "center", marginBottom: "10px" }}>
                <span style={{ marginRight: "10px" }}></span> Ti xung khng gii hn ti sn Premium
              </li>
              <li style={{ display: "flex", alignItems: "center" }}>
                <span style={{ marginRight: "1px" }}></span> Ha n trc tuyn
              </li>
            </ul>
          </div>

          <div
            className="ppt-btn-group"
            style={{ display: "flex", justifyContent: "space-between", marginTop: "10px" }}
          >
            <button className="ppt-btn ppt-btn-gray" style={{ flex: "1", marginRight: "5px" }}>
              <span style={{ marginRight: "5px" }}></span> Giy php c nhn
            </button>
            <button className="ppt-btn ppt-btn-green" style={{ flex: "1" }}>
              <span style={{ marginRight: "5px" }}></span> Nng cp gi ngay hm nay
            </button>
          </div>
        </div>
      </div>

      {/* Phn nh gi */}
      <div className="ppt-reviews">
        <h2>nh gi</h2>
        <div className="rating-summary">
          <div className="average-rating">
            <span>{averageRating}</span>/5
          </div>
          <div className="stars">
            {[...Array(5)].map((_, index) => (
              <i
                key={index}
                className={`bx ${index < Math.round(averageRating) ? "bxs-star" : "bx-star"}`}
              ></i>
            ))}
          </div>
          <span>({reviews.length} nh gi)</span>
        </div>

        <div className="review-list">
          {reviews.length === 0 ? (
            <p>Cha c nh gi no.</p>
          ) : (
            reviews.map((review, index) => (
              <div key={index} className="review-item">
                <div style={{ display: "flex", justifyContent: "space-between", marginBottom: "5px" }}>
                  <div>
                    <span style={{ fontWeight: "bold" }}>{review.user}</span>
                    <div className="stars">
                      {[...Array(5)].map((_, i) => (
                        <i
                          key={i}
                          className={`bx ${i < Math.round(review.rating) ? "bxs-star" : "bx-star"}`}
                        ></i>
                      ))}
                    </div>
                  </div>
                  <span style={{ color: "#666", fontSize: "14px" }}>{review.date}</span>
                </div>
                <p style={{ margin: "5px 0 0 0" }}>{review.comment}</p>
              </div>
            ))
          )}
        </div>
      </div>

      {/* Mu PowerPoint thnh hnh */}
      <section className="top-categories top">
        <h1 className="heading-1">Mu PowerPoint thnh hnh</h1>
        <div className="container-categories">
          {relatedPowerpoints.slice(0, 5).map((category) => (
            <div className="card-category" key={category.id}>
              <img src={category.duong_dan_anh_nho} alt={category.tieu_de} className="template-img" />
              <div className="overlay">
                <span className={category.la_pro ? "badge-pro" : "badge-free"}>
                  {category.la_pro ? "Pro" : "Min ph"}
                </span>
                <button className="download-btn">
                  <i className="bx bx-download"></i> PowerPoint
                </button>
                <p className="template-title">{category.tieu_de}</p>
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* Hnh nn sng to */}
      <section className="top-categories top">
        <h1 className="heading-1">Hnh nn sng to</h1>
        <div className="container-categories">
          {relatedBackgrounds.slice(0, 4).map((item) => (
            <div className="card-category-1" key={item.id}>
              <img src={item.duong_dan_anh_nho} alt={item.tieu_de} width={350} height={200} />
              <div className="overlay">
                <span className={item.la_pro ? "badge-pro" : "badge-free"}>
                  {item.la_pro ? "Pro" : "Min ph"}
                </span>
                <button className="download-btn">Xem thm v b su tp</button>
                <p className="template-title">{item.tieu_de}</p>
              </div>
            </div>
          ))}
        </div>
        <div className="container-categories top">
          {relatedBackgrounds.slice(4, 8).map((item) => (
            <div className="card-category-1" key={item.id}>
              <img src={item.duong_dan_anh_nho} alt={item.tieu_de} width={350} height={200} />
              <div className="overlay">
                <span className={item.la_pro ? "badge-pro" : "badge-free"}>
                  {item.la_pro ? "Pro" : "Min ph"}
                </span>
                <button className="download-btn">Xem thm v b su tp</button>
                <p className="template-title">{item.tieu_de}</p>
              </div>
            </div>
          ))}
        </div>
      </section>
    </div>
  );
};

export default PPTTemplate;